.GiftItem_input_image__1jz4k:hover {
    box-shadow: 0px 8px 12px 3px rgba(0,5,5,0.25);
    -webkit-transform: perspective(500px) translateZ(5px);
            transform: perspective(500px) translateZ(5px);
    -webkit-animation: GiftItem_likebounce__3th5B 0.3s ease-in-out 1;
    animation: GiftItem_likebounce__3th5B 0.3s ease-in-out 1;
}
  
@-webkit-keyframes GiftItem_likebounce__3th5B {
    0%   {
        -webkit-transform: perspective(500px) translateZ(0px);
        transform: perspective(500px) translateZ(0px);
    }

    100% {
        -webkit-transform: perspective(500px) translateZ(5px);
        transform: perspective(500px) translateZ(5px);
    }
}
  
@keyframes GiftItem_likebounce__3th5B {
    0%   {
        -webkit-transform: perspective(500px) translateZ(0px);
        transform: perspective(500px) translateZ(0px);
    }

    100% {
        -webkit-transform: perspective(500px) translateZ(5px);
        transform: perspective(500px) translateZ(5px);
    }
}

